import { Routes } from '@angular/router';

import { DashboardComponent } from './dashboard-v1/dashboard.component';

export const DashboardRoutes: Routes = [{
  path: '',
  redirectTo: 'home',
  pathMatch: 'full',
},{
  path: '',
  children: [{
    path: 'home',
    component: DashboardComponent
  },]
}];



