import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {SettingsService} from "../../core/settings/settings.service";
import {AuthHttpService} from "../../auth/auth-http.service";
import {BaseCardChartBean} from "../metier/basecardchartbean.metier";
import {BarBaseChartBean} from "../metier/barbasechartbean.metier";
import {LineBaseChartBean} from "../metier/linebasechartbean.metier";

@Injectable()
export class DashBoardService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  getBaseCardBean(): Observable<BaseCardChartBean> {
    return this.authHttp.post(this.settings.baseUrl + '/dashBoard/getBaseCardBean')
      .map((res: Response) => res.json());
  }

  getRevenue(): Observable<Array<BarBaseChartBean>> {
    return this.authHttp.get(this.settings.baseUrl + `/dashBoard/getRevenue`)
      .map((res: Response) => res.json());
  }

  getMaintenance(): Observable<Array<LineBaseChartBean>> {
    return this.authHttp.get(this.settings.baseUrl + `/dashBoard/getMaintenance`)
      .map((res: Response) => res.json());
  }

  getFacturation(): Observable<Array<BarBaseChartBean>> {
    return this.authHttp.get(this.settings.baseUrl + `/dashBoard/getFacturation`)
      .map((res: Response) => res.json());
  }

  getCharge(): Observable<Array<LineBaseChartBean>> {
    return this.authHttp.get(this.settings.baseUrl + `/dashBoard/getCharge`)
      .map((res: Response) => res.json());
  }

}



