import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { PageTitleService } from '../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { DashBoardService } from "../service/dashboard.service";
import { BaseComponent } from "../../core/component/base-component";
import { InterventionService } from '../../intervention/service/intervention.service';
import { Intervention } from '../../intervention/metier/intervention.metier';

@Component({
  selector: 'ms-dashboard',
  templateUrl: './dashboard-component.html',
  styleUrls: ['./dashboard-component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class DashboardComponent extends BaseComponent implements OnInit {

  // public orders: any[];
  public revenue: any[] = new Array();
  public maintenance: any[] = new Array();
  public facturation: any[] = new Array();
  public charge: any[] = new Array();
  private interventionList: Array<Intervention> = [];

  public nbrClient: number = 0;
  public totalPaye: number = 0;
  public totalNPaye: number = 0;
  public totalFacturePaye: number = 0;
  public totalFactureNPaye: number = 0;
  public totalCharge: number = 0;
  private totalRevenue: number = 0;
  private totalMaintenance: number = 0;
  private totalFacturationReste: number = 0;
  private totalChargeMansuel: number = 0;

  constructor(private pageTitleService: PageTitleService, private dashBoardService: DashBoardService, private interventionService: InterventionService) {
    super();

  }

  ngOnInit() {
    this.pageTitleService.setTitle("Home");

    this.loadBaseCardChartBean();


    setTimeout(() => this.loadRevenue());
    setTimeout(() => this.loadMaintenance());
    setTimeout(() => this.loadFacturation());
    setTimeout(() => this.loadCharge());

    this.loadInterventionList();
  }

  private loadRevenue() {
    this.dashBoardService.getRevenue()
      .subscribe(revenueList => {
        this.revenue = revenueList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadMaintenance() {
    this.dashBoardService.getMaintenance()
      .subscribe(maintenanceList => {
        this.maintenance = maintenanceList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadFacturation() {
    this.dashBoardService.getFacturation()
      .subscribe(facturationList => {
        this.facturation = facturationList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadCharge() {
    this.dashBoardService.getCharge()
      .subscribe(chargeList => {
        this.charge = chargeList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadBaseCardChartBean() {
    this.dashBoardService.getBaseCardBean()
      .subscribe(baseCardChartBean => {
        this.nbrClient = baseCardChartBean.nbrClient;
        this.totalPaye = baseCardChartBean.totalPaye;
        this.totalNPaye = baseCardChartBean.totalNPaye;
        this.totalFacturePaye = baseCardChartBean.totalFacturePaye;
        this.totalFactureNPaye = baseCardChartBean.totalFactureNPaye;
        this.totalCharge = baseCardChartBean.totalCharge;
        this.totalRevenue = baseCardChartBean.totalRevenue;
        this.totalMaintenance = baseCardChartBean.totalMaintenance;
        this.totalFacturationReste = baseCardChartBean.totalFacturationReste;
        this.totalChargeMansuel = baseCardChartBean.totalChargeMansuel;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadInterventionList() {
    this.interventionService.getlastInterventionList()
      .subscribe(interventionList => {
        this.interventionList = interventionList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  public colorScheme = {
    domain: ['#FFFFFF']
  };
  public autoScale = true;

  public onSelect(event) {
    console.log(event);
  }

  ngOnDestroy() {
  }


}

















